* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --head_size: 60px;
  --mapDiv_Width: 60vw;
}
body {
  background: lightslategrey;
  font-family: 'Nunito', sans-serif;
}

header {
  margin: 0 auto;
  padding: 10px 0;
  background: lightslategray;
  text-align: center;
   font-size: 30px;
   text-transform: uppercase;
   font-weight: bold;
   max-height: var(--head_size);
}

.main {
  display: flex;
  width: 90%;
  margin: 0 auto;
  background: white;
  height: calc(100vh - var(--head_size));
}
 .map {
  position: relative;
  width: var(--mapDiv_Width);
}

.table {
  width: calc(100vw - var(--mapDiv_Width));
  overflow: visible;
}

.map svg {
  position: absolute;
  width: 80%;
  height: auto;
  fill: lightseagreen;
  left: calc(var(--mapDiv_Width)*0.1);
}
.map g {
  cursor: pointer;
  transition: all 0.7s ease-in;
}

.map path {
 stroke: white;
}

.text {
  fill: midnightblue;
  font-weight: bold;
  font-size: 13px;
}

.map g:hover {
  fill: #cbe8f0;
}

.popup span {
  font-weight: bold;
  color: blue;
}

.activeRow {
  background: lightseagreen !important;
}
